  border: 3px solid #ffffff;
  background: rgba(48, 194, 198, 0.7);
  backdrop-filter: blur(11px);
  border-radius: 2px;
  padding: 15px 20px;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s ease;
}
.tooltip--open {
  opacity: 1;
}
.tooltip__stick {
  position: absolute;
  top: 50%;
  right: calc(100% - 5px);
  width: 42px;
  height: 2px;
  background-color: #ffffff;
}
.tooltip__stick--vertical {
  top: -19px;
  right: unset;
  left: 50%;
  transform: translateX(-50%) rotate(90deg);
}
.tooltip__stick::before, .tooltip__stick::after {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ffffff;
}
.tooltip__stick::before {
  left: 0;
}
.tooltip__stick::after {
  right: 0;
}
.tooltip__image {
  margin: 0 auto;
}
.tooltip__image img {
  width: auto;
  max-height: 250px;
  margin: 0 auto;
}
.tooltip__title, .tooltip__text {
  text-align: center;
}
.tooltip__title {
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  margin-top: 20px;
}
.tooltip__text {
  font-size: 13px;
  margin-top: 10px;
}